import React, {Component} from 'react';
import { NavLink } from 'react-router-dom';

class Link extends Component {
    render(){
        return (
            <NavLink 
                className = {`is${this.props.isAble} btnLink`} 
                to = {`/${this.props.anchor}`}
                >
                {this.props.addressName}
            </NavLink>
        );
    }
}

export default Link;