import React, {Component} from 'react';
import Link from './links';

class ListLinks extends Component{
    constructor(props){
        super(props);
        this.state = { linkAbled: "idDisabled" }
    }
    render(){
        const titleExercises = <h3 className="typeFile">Ejercicios</h3>
        const titlePractices = <h3 className="typeFile">Practicas</h3>
        let practices = [];
        let exercises = [];
        const sites = this.props.sites;
        const isAble = this.props.able;
        for(let i = 0; i<sites.length;i++){
            if(sites[i][1].match(/\bEjerc/g)){
                exercises.push(<Link key={i} anchor={sites[i][0]} addressName={sites[i][1]} isAble={isAble} />)
            } else {
                practices.push(<Link key={i} anchor={sites[i][0]} addressName={sites[i][1]} isAble={isAble} />)
            }
        }
        return(
            <div id={`list${this.props.keyNum}-practices`}>
                {exercises.length>0 && [titleExercises, exercises]}
                {practices.length>0 && [titlePractices, practices]}
            </div>
        );
    }
}

export default ListLinks;