import React,{Component} from 'react';
import ListLinks from './listLinks';

class ToggleButton extends Component{
    constructor(props){
        super(props);
        this.state = { btnPressed: false }
    }
    render(){
        const key = this.props.keyNum
        const sites = this.props.sites
        let btnState;
        return(
            <div className="practice">
                <input className="check"
                onChange ={ () => {
                    btnState = !this.state.btnPressed
                    setTimeout(()=>this.setState({btnPressed:btnState}), 200)
                }}
                id={"title"+key} type="checkbox"/>
                <label className="toggle" htmlFor={"title"+key}>{"Unidad "+key}</label>
                {this.state.btnPressed && <ListLinks keyNum={key} sites={sites} able={btnState}/>}
            </div>
        );
    }
}

export default ToggleButton;