import React, {Component} from 'react';
import { Route } from 'react-router-dom';

class ViewRoutes extends Component {
    render(){
        const view_path = this.props.routeView;
        const view_component = this.props.compView;
        return (
            <Route path={`/${view_path}`} component={view_component}/>
        );
    }
}

export default ViewRoutes;