import React, { Component } from 'react';
import { HashRouter, Route, Redirect } from 'react-router-dom';
import Home from './views/home'
import ExerciseOne from './views/unit1/exercise1';
import ExerciseTwo from './views/unit1/exercise2';
import ExerciseThree from './views/unit1/exercise3';
import ExerciseThreeOne from './views/unit1/exercise3_1';
import ExerciseThreeTwo from './views/unit1/exercise3_2';
import ExerciseFour from './views/unit1/exercise4';
import PracticeOne from './views/unit2/practice1';
import PracticeTwo from './views/unit2/practice2';
import ToggleButton from './components/toggleButton';
import ViewRoutes from './components/routes';
import './styles.css';

let array_sites = [
  [
    ['exerciseOne', 'Ejercicio 1', ExerciseOne],
    ['exerciseTwo', 'Ejercicio 2', ExerciseTwo],
    ['exerciseThree', 'Ejercicio 3', ExerciseThree],
    ['exerciseFour', 'Ejercicio 4', ExerciseFour]
  ],
  [
    ['practiceOne', 'Practica 1', PracticeOne],
    ['practiceTwo', 'Practica 2', PracticeTwo]
  ]
]

class App extends Component {
  render() {
    let routes = [];
    const toggleButton = array_sites.map((sites, index)=>{
      return (<ToggleButton key={index} keyNum={index+1} sites={sites}/>)
    });
    for(let i = 0; i<array_sites.length; i++){
      for(let j = 0; j<array_sites[i].length; j++){
        routes.push(<ViewRoutes routeView={array_sites[i][j][0]} compView={array_sites[i][j][2]} key={`${i}+${j}`}/>)
      }
    }
    return (
      <HashRouter>
        <div className="mainPage">
          <div className="menu">
            <div className="back"></div>
              <div className="title">
                <p>Portafolio</p>
              </div>
              <div className="content">
              <div className="practices-content">
                {toggleButton}
              </div>
            </div>
          </div>
          <div className="view-content">
            <Redirect from="/" exact to="/home"/>
            <Route path="/home" component={Home}/>
            {routes}
            <Route path="/exerciseThreeOne" component={ExerciseThreeOne}/>
            <Route path="/exerciseThreeTwo" component={ExerciseThreeTwo}/>
          </div>
        </div>
      </HashRouter>
    );
  }
}

export default App;
